<template>
    <div class="edit-wrap">
        <!--<HeaderTop :isLogin="isLogin" :userInfo="userInfo"></HeaderTop>-->
        <div class="upload-cover" id="QINIU" @mouseenter="showTxt" @mouseleave="showTxt">
            <div class="bg" ref="coverbg" >
                <div class="img-prompt" v-if="!isUpload">
                    <div><svg class="icon" aria-hidden="true"><use xlink:href="#icon-image"></use></svg></div>
                    <div class="txt" ref="txt"></div>    
                </div>
            </div>
            <input type="file" id="qiniu-upload" accept=".jpg,.png,.gif">
        </div>
        <div class="title-input">
            <input type="text" placeholder="">
        </div>
        <div class="editor">
            <quillEditor
                v-model="content"
                ref="quillEditor"
                :options="editorOptions"
            ></quillEditor>
        </div>
    </div>
</template>

<script>
import HeaderTop from './Header';

import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

import '../static/js/plupload/plupload.full.min';
import '../static/js/qiniu.min';

import options from '../static/config/editor_config';
import { quillEditor } from 'vue-quill-editor'
export default {
    name: 'Edit',
    data() {
        return {
            content:'',
            editorOptions : options,
            isUpload:false
        }
    },
    props:['isLogin','userInfo'],
    // components:{HeaderTop},
    mounted: function () {
        document.title = '';
        console.log();
        this.uploadImg();
    },

    methods:{
        editor(){
            return this.$refs.quillEditor.quill;
        },
        uploadImg(){
            var _this = this;
            var uploader = Qiniu.uploader({
                disable_statistics_report: false, // 
                runtimes: 'html5,flash,html4', // ,
                browse_button: 'qiniu-upload', // ****
                get_new_uptoken: true, //  uptoken                
                uptoken_url: '/api/gettoken',         // Ajax  uptoken  Url****
                // uptoken_func: function(data){    //  uptoken 
                //     // return data.uptoken;
                // },
                unique_names: false,
                save_key: false,
                domain: 'http://oy02r7jyx.bkt.clouddn.com', // bucket 'http://xxx.bkt.clouddn.com/' ****
                container: 'QINIU', //  DOM ID browser_button 
                max_file_size: '100mb', // 
                flash_swf_url: '../static/js/plupload/Moxie.swf', // flash,
                max_retries: 3, // 
                dragdrop: true, // 
                drop_element: 'QINIU', //  ID
                chunk_size: '4mb', // 
                auto_start: true, // ,
                init: {
                    'FilesAdded': function (up, files) {
                        plupload.each(files, function (file) {
                            // ,
                        });
                    },
                    'BeforeUpload': function (up, file) {
                        // ,
                    },
                    'UploadProgress': function (up, file) {
                        // ,
                    },
                    'FileUploaded': function (up, file, info) {
                        var domain = up.getOption('domain');
                        var res = JSON.parse(info.response);
                        var sourceLink = domain +'/'+ res.key; //Url
                        _this.isUpload = true;
                        _this.$refs.coverbg.style.cssText += 'background:url('+sourceLink+') center center / cover no-repeat;'
                    },  
                    'Error': function (up, err, errTip) {
                        console.log(up,err,errTip)
                        
                    },
                    'UploadComplete': function () {
                        //,
                    },
                    'Key': function (up, file) {
                        return 'cover_pic/'+file.name
                    }
                }
            });
        },
        showTxt(e){
            if(this.isUpload) return;
            if(e.type === 'mouseenter'){
                this.$refs.txt.style.display = 'block';
            }else{
                this.$refs.txt.style.display = 'none';
            }
        }
    },
    components: {
        quillEditor
    }

}
</script>
<style lang="scss">
    #app{
        box-sizing:border-box;
        padding:0;
        min-height:100vh;
    }
    .edit-wrap{
        width:800px;
        margin:0 auto;
        .upload-cover{
            height:200px;
            background:#f2f2f2;
            position:relative;
        }
        input[type="file"]{
            display:block;
            width:100%;
            height:100%;
            position:absolute;
            top:0;
            left:0;
            z-index:200;
            cursor:pointer;
            opacity:0;
        }
        .bg{
            width:100%;
            height:100%;
            position:absolute;
            top:0;
            left:0;
            z-index:100;
        }
        .img-prompt{
            position:absolute;
            top:50%;
            left:50%;
            transform:translate(-50%,-50%);
            color:#bcbcbc;
            .icon{
                font-size:30px;
            }
            .txt{
                font-size:16px;
                text-align:center;
                display:none;
            }
        }
        .title-input{
            height:30px;
            margin:20px 0;
            input{
                width:100%;
                height:100%;
                font-size:24px;
                text-align:left;
            }
        }
    }
    .quill-editor .ql-toolbar .ql-formats{
        &:last-child{
            border-right:none;
        }
        margin-right:0;
        padding: 0 5px;
        border-right:1px solid #dee0e1;
        
    }
    .quill-editor{
        .ql-toolbar{
            text-align:left;
        }
        .ql-picker{
            border:1px solid #dee0e1;
            margin:0 5px;
        }
        .ql-toolbar button{
            border:1px solid #dee0e1;
            margin: 0 5px;
        }
        .ql-editor{
            min-height:300px;
            font-size:16px;
            blockquote{
                margin:0;
            }        
        }
    }
    
</style>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
    .ql-toolbar .ql-formats{
        margin-right:0;
    }
</style>



